* {
    margin: 0px;
    padding: 0px;


    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    /* font-size: 16px; */
    color: #666666;
    outline: none;
    text-decoration: none;
}

html{
    scroll-behavior: smooth;
    font-size: 10px;
}

ul ,ol ,li {
    list-style: none;
}

.text-center{
    text-align: center;
}

.margin-7vw{
    margin-left: 7.5vw;
    margin-right: 7.5vw;
}


@media (max-width: 300px){
    
    html{
         font-size: 8px ;
        }
}